// import Color from 'color'
import React from 'react';
import createReactClass from 'create-react-class'
import styled, {keyframes} from 'styled-components'
// import PropTypes from 'prop-types'
var injectTapEventPlugin = require('react-tap-event-plugin')
injectTapEventPlugin();


const animateBgColor = keyframes`
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
`;
const App = styled.div `
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(270deg, #062920, #1a6555, #065040);
  background-size: 400% 100%;
  animation: ${animateBgColor} 80s ease infinite;
`


module.exports = createReactClass({
  getInitialState: function () {
    return {
      input: [],
    }
  },
  render: function () {

    const MobileScreen = styled.div`
      position: relative;
      width: 375px;
      height: 667px;
      background-color: #333;
      opacity: 0.95;
    `
    const Main = styled.div`
      position: relative;
      height: ${(100 * (2/3)) - 5}%;
      color: white;
    `
    const Pass = styled.div`
      position: absolute;
      bottom: 0;
      display: flex;
      padding: 0 15% 5%;
      width: 70%;
      height: 12%
    `
    const PassBox = styled.div`
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 7%;
      width: 21%;
      height: 100%;
      margin: 0 2%;
      text-align: center;
      line-height: 1rem;
      font-size: 3rem;
      color: #0feab2;
      text-shadow: 0px 0px 15px #0feab2;
      background-color: #444;
    `
    const Dialpad = styled.div`
      position: absolute;
      bottom: 0;
      display: flex;
      flex-wrap: wrap;
      padding: 5%;
      width: 90%;
      height: 30%;
    `
    //Hack for non-standard HTML attributes (e.g., onTouchTap)
    //Reference: https://github.com/styled-components/styled-components/issues/527#issuecomment-281931998
    const Key = styled((props) => <button {...props}/>)`
      margin: 0.8%;
      width: ${100/3 - 2}%;
      font-size: 1.1em;
      font-family: Roboto, sans-serif;
      color: #ccc;
      border: none;
      border-radius: 3px;
      cursor: pointer;
      transition: all 1000ms ease-out;
      background-color: #555;
      outline-style: none;
      &:active {
        color: #555;
        background-color: #0feab2;
      }
    `

    return (
      <App>
        <MobileScreen>
          <Main>
            <Pass>
              <PassBox>{this.state.input[0] ? '' : ''}</PassBox>
              <PassBox>{this.state.input[1] ? '' : ''}</PassBox>
              <PassBox>{this.state.input[2] ? '' : ''}</PassBox>
              <PassBox>{this.state.input[3] ? '' : ''}</PassBox>
            </Pass>
          </Main>
          <Dialpad>
            <Key onTouchTap={this.handleInput.bind(null, '1')}>1</Key>
            <Key onTouchTap={this.handleInput.bind(null, '2')}>2</Key>
            <Key onTouchTap={this.handleInput.bind(null, '3')}>3</Key>

            <Key onTouchTap={this.handleInput.bind(null, '4')}>4</Key>
            <Key onTouchTap={this.handleInput.bind(null, '5')}>5</Key>
            <Key onTouchTap={this.handleInput.bind(null, '6')}>6</Key>

            <Key onTouchTap={this.handleInput.bind(null, '7')}>7</Key>
            <Key onTouchTap={this.handleInput.bind(null, '8')}>8</Key>
            <Key onTouchTap={this.handleInput.bind(null, '9')}>9</Key>

            <Key onTouchTap={this.deleteInput}>X</Key>
            <Key onTouchTap={this.handleInput.bind(null, '0')}>0</Key>
            <Key onTouchTap={this.resetInput}>.</Key>
              </Dialpad>
        </MobileScreen>
      </App>
    )
  },
  handleInput: function (value) {

    console.log(1, value)

    this.setState({
      input: this.state.input.length !== 4 ? this.state.input.concat(value) : [value]
    })
  },
  deleteInput: function () {

    this.setState({
      input: this.state.input.slice(0, -1)
    })
  },
  resetInput: function () {

    this.setState({input: []})
  }
})



